import { Menu } from 'antd';
import { AppstoreOutlined, MailOutlined, SettingOutlined } from '@ant-design/icons';
import React from 'react';

const { SubMenu } = Menu;

// submenu keys of first level
const rootSubmenuKeys = ['sub1', 'sub2', 'sub4'];
let Sider;
export default  Sider = () => {
  const [openKeys, setOpenKeys] = React.useState(['sub1']);

  const onOpenChange = keys => {
    const latestOpenKey = keys.find(key => openKeys.indexOf(key) === -1);
    if (rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
      setOpenKeys(keys);
    } else {
      setOpenKeys(latestOpenKey ? [latestOpenKey] : []);
    }
  };

  return (
    <Menu mode="inline" openKeys={openKeys} onOpenChange={onOpenChange} style={{ position:'absolute',width:'20%',height:'100%',backgroundColor:'white' }}>
      <SubMenu  style={{width:'115%'}} key="sub1" icon={<MailOutlined />} title="">
      </SubMenu>
      <SubMenu   style={{width:'115%'}}  key="sub2" icon={<AppstoreOutlined />} title="">
      </SubMenu>
      <SubMenu   style={{width:'115%'}} key="sub4" icon={<SettingOutlined />} title="Navigation">
      </SubMenu>
      {/* <li>sss</li> */}
    </Menu>
  );
};

